import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { Storage } from '@ionic/storage';
import { LoginPage } from '../login/login';

import { AppServiceProvider } from '../../providers/app-service/app-service';


@IonicPage()
@Component({
  selector: 'page-information',
  templateUrl: 'information.html',
})
export class InformationPage {

  name: string = "";
  infoParam: string = "";
  messages: string = "";
  constructor(public navCtrl: NavController, public navParams: NavParams, public storage: Storage, public appService: AppServiceProvider) {
    this.infoParam = this.navParams.get('infoParam');
    this.init();
  }

  init() {
    this.storage.get('member').then(member => {
      this.name = member.FullName;
      console.log(member);

      if (this.infoParam == "1-2") {
        this.messages = "ye girii yapp anketleri doldurduunuz iin teekkr ederiz. imdilik lohusalk sreciniz tamamlana kadar bekleyip, tamamladnzda son iki anketimizi de doldurmanz rica edeceiz. <br /><br /> Tm yelerimiz bu sreci tamamladnda uygulama tamamen herkesin kullanmna alacaktr."
      }
      else if (this.infoParam == "3-4") {
        this.messages = "Lohusalk srecinizi tamamladnz tebrik ederiz. Tm anketleri de doldurdunuz ilginiz iin teekkrler. imdi uygulamadaki herkesin srecini tamamlamas iin ksa bir sre uygulamaya eriiminiz kstlanmtr. <br /><br /> Tm yelerimiz bu sreci tamamladnda uygulama tamamen tm yelerimize alacaktr.";
      }
      else if (this.infoParam == "block") {
        //this.messages = "Hesabnz bir sreliine kullanm ddr. Bu durum uyugulamay aktif kullanmaynzdan kaynakl olabilir. <br /><br /> Detayl bilgi iin uygulama yneticisiyle irtibata geebilirsiniz.";
        this.checkSurvey(member);
      }


    });
  }

  checkSurvey(member) {
    this.appService.checkSurveyCompleted(member.MemberId).subscribe(surveyResult => {
      let isFirstSurveyCompleted = this.checkSurveyCompleted(surveyResult.Result, 1);
      let isSecondSurveyCompleted = this.checkSurveyCompleted(surveyResult.Result, 2);
      let isThirdSurveyCompleted = this.checkSurveyCompleted(surveyResult.Result, 3);
      let isFourthSurveyCompleted = this.checkSurveyCompleted(surveyResult.Result, 4);

      if(isFirstSurveyCompleted) {
        if(isSecondSurveyCompleted){
          if(isThirdSurveyCompleted){
            if(isFourthSurveyCompleted){
              this.messages = "Lohusalk srecinizi tamamladnz tebrik ederiz. Tm anketleri de doldurdunuz ilginiz iin teekkrler. imdi uygulamadaki herkesin srecini tamamlamas iin ksa bir sre uygulamaya eriiminiz kstlanmtr. <br /><br /> Tm yelerimiz bu sreci tamamladnda uygulama tamamen tm yelerimize alacaktr.";
            }
            else {
              this.messages = "Hesabnz bir sreliine kullanm ddr. Bu durum uyugulamay aktif kullanmaynzdan kaynakl olabilir. <br /><br /> Detayl bilgi iin uygulama yneticisiyle irtibata geebilirsiniz.";
            }
          }
          else {
            this.messages = "Hesabnz bir sreliine kullanm ddr. Bu durum uyugulamay aktif kullanmaynzdan kaynakl olabilir. <br /><br /> Detayl bilgi iin uygulama yneticisiyle irtibata geebilirsiniz.";
          }
        }
        else {
          this.messages = "Hesabnz bir sreliine kullanm ddr. Bu durum uyugulamay aktif kullanmaynzdan kaynakl olabilir. <br /><br /> Detayl bilgi iin uygulama yneticisiyle irtibata geebilirsiniz.";
        }
      }
      else {
        this.messages = "Hesabnz bir sreliine kullanm ddr. Bu durum uyugulamay aktif kullanmaynzdan kaynakl olabilir. <br /><br /> Detayl bilgi iin uygulama yneticisiyle irtibata geebilirsiniz.";
      }

    });
  }

  checkSurveyCompleted(data, surveyId) {
    let flag = false;

    data.forEach(item => {
      if (item.SurveyTypeId == surveyId) {
        flag = true;
      }
    });

    return flag;

  }

  okBtnClick() {
    this.navCtrl.push(LoginPage);
  }


}



